@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');

.App {
  background: rgb(0,7,77);
  background: linear-gradient(135deg, rgba(0,7,77,1) 0%, rgba(0,6,116,1) 54%, rgba(0,161,190,1) 100%);
  height: 100vh;
  box-sizing: border-box;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-header {
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}